import React, { Component } from 'react';
import './Clock.scss';

class Clock extends Component {
	constructor(props) {
		super(props);
		this.state = {
			fullDate: '',
			hours: 0,
			minutes: '',
			seconds: '',
		};
	}

	updateDate() {
		let date = new Date(),
			fullDate = date.toLocaleDateString('en-EN', {
				weekday: 'long',
				month: 'long',
				day: 'numeric',
			});
		this.setState({
			fullDate: fullDate,
		});
	}

	updateTime() {
		let date = new Date(),
			hours = date.getHours(),
			minutes = date.getMinutes(),
			seconds = date.getSeconds();

		hours < 10 ? hours = '0' + hours.toString() : hours.toString();
		minutes < 10 ? minutes = '0' + minutes.toString() : minutes.toString();
		seconds < 10 ? seconds = '0' + seconds.toString() : seconds.toString();

		this.setState({
			hours: hours,
			minutes: minutes,
			seconds: seconds,
		});
	}

	currentTime() {
		this.updateDate();
		this.updateTime();
		this.timerID = setInterval(() => {
			this.updateTime();
		}, 1000);
	}

	componentDidMount() {
		this.currentTime();
	}

	componentWillUnmount() {
		clearInterval(this.timerID);
	}

	render() {
		const { hours, minutes, seconds } = this.state;
		return (
			<div className='timedate'>
				<div className='caption'>Today is</div>
				<div className='full-date'>{this.state.fullDate}</div>
				<hr />
				<div className='time'>
					<div className='hours'>{hours}</div>
					<div className='minutes'>{minutes}</div>
					<div className='seconds'>{seconds}</div>
				</div>
			</div>
		);
	}
}

export default Clock;
